import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { Http, Response, RequestOptions, Headers } from "@angular/http";
import { Observable } from "rxjs/Observable";
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, ActivatedRoute, Params } from '@angular/router';
import "rxjs/Rx";
import { CommonService } from "../services/commonService";
import { MaintenanceService } from "../services/maintenanceService";
import swal from 'sweetalert2';

declare interface DataTable {
    mTanceHeaderRow: string[];
    mTanceFooterRow: string[];
    mTanceDataRows: any;
}
declare const $: any;

@Component({
    selector: 'app-maintenance',
    templateUrl: './maintenance.component.html',
    styleUrls: ['./maintenance.component.scss'],
    providers: [CommonService, MaintenanceService]
})
export class MaintenanceComponent implements OnInit {
    public mTanceDataTable: DataTable;
    public maintenanceList: any;

    constructor(
        private http: Http,
        private router: Router,
        private commonService: CommonService,
        private maintenanceService: MaintenanceService,
        private activatedRoute: ActivatedRoute,
    ) { }

    ngOnInit() {
        if (JSON.parse(sessionStorage.getItem('LogEmployee')) != null) {
            this.getMaintenanceDetails();
            this.mTanceDataTable = {
                mTanceHeaderRow: ['MT#', 'Job-Wo', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                mTanceFooterRow: ['MT#', 'Job-Wo', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                mTanceDataRows: this.maintenanceList
            };
        } else {
            this.router.navigate(['pages/login']);
        }
    }

    getMaintenanceDetails() {
        $('.loader').show();
        this.maintenanceService.GetMaintenanceDetails(this.commonService.baseApiUrl).subscribe(
            data => {
                var dataMtance = JSON.stringify(data);
                var mtanceParse = JSON.parse(dataMtance);
                var mTanceList = JSON.parse(mtanceParse["_body"]);
                this.maintenanceList = null;
                if (mTanceList != null) {
                    $("#mTanceDatatables").dataTable().fnDestroy();
                    this.maintenanceList = mTanceList;

                    this.mTanceDataTable = {
                        mTanceHeaderRow: ['MT#', 'Job-Wo', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],//'ID'
                        mTanceFooterRow: ['MT#', 'Job-Wo', 'Type', 'Raised By', 'Date', 'Status', 'Actions'],
                        mTanceDataRows: this.maintenanceList
                    };

                    setTimeout(() => {
                        $("#mTanceDatatables").dataTable().fnDestroy();
                        $('#mTanceDatatables').DataTable({
                            'pagingType': 'full_numbers',
                            'lengthMenu': [[10, 25, 50, -1], [10, 25, 50, 'All']],
                            responsive: true,
                            language: {
                                search: '_INPUT_',
                                searchPlaceholder: 'Search records',
                            }

                        });

                        const tableWO = $('#mTanceDatatables').DataTable();

                        // Edit record
                        tableWO.on('click', '.edit', function () {

                        });

                        // Delete a record
                        tableWO.on('click', '.remove', function (e: any) {
                            const $trWO = $(this).closest('tr');
                            tableWO.row($trWO).remove().draw();
                            e.preventDefault();
                        });

                        // Like record
                        tableWO.on('click', '.like', function () {
                            alert('You clicked on Like button');
                        });

                    }, 10);

                    $('.loader').hide();
                }
            }
        );
    }
    goToCreateMaintenance(mtId: any) {
        this.router.navigate(['create-maintenance'], { queryParams: { mtid: mtId } });
    }

}



